<template>
  <div ref="trys" style="height: calc(100% - 0em)">
    <div v-if="addOrInfo" class="info-container">
      <edit :controls="controls" :row="row" @closeDiv="closeDiv"></edit>
    </div>
    <div v-show="!addOrInfo" class="all-container">
      <div class="operate-div">
        <template v-for="(item, $index) in FormOpt">
          <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom"></inputs>
          <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
        </template>
      </div>
      <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 4.5em)'}">
        <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding" @rowDbClick="rowDbClick"></tables>
      </div>
    </div>
    <dialogs type="confirm" ref="conformDialog"></dialogs>
    <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>
import edit from './EditorPlan.vue'
export default {
  data() {
    return {
      searchParam: {
        schemeType: '',
        schemeName: '',
        remark: '',
      },
      tableHeight: '',
      showTable: false,
      addOrInfo: false,
      controls: [],
      FormOpt: [
            {
                    type: 'select',
                    options: {
                      key: 'searchParam.schemeType',
                      cs: 'server',
                      url: 'dictItem/queryByDictCode',
                      param: {dictCode: '16'},
                      rowName: 'data',
                       text: 'dictItemName',
                      value: 'dictItemValue',
                      method: 'post',
                      width: '8em',
                      hasAll: true
                    }
                  },
           {
                type: 'input',
                options: {
                  type: 'typeSearch',
                  key: 'searchParam',
                  selectOpt: {
                    width: '7em',
                    cs: 'client',
                    data: [
                      {text: '方案名称', value: 'schemeName'},
                      {text: '方案描述', value: 'remark'}
                    ]
                  }
                }
       }],
      TableOpt: {
        column: [
          { title: '方案名称', field: 'schemeName', textAlign: 'center', width: '80' },
          { title: '方案类别', 
            field: 'schemeTypeName', 
            textAlign: 'center', 
            width: '120'
          },
          { title: '电表数', field: 'number', textAlign: 'center', width: '70' },
          { title: '创建人', field: 'creator', textAlign: 'center', width: '80' },
          { title: '创建时间', field: 'createTime', textAlign: 'center', width: '80' },
          { title: '描述', field: 'desc', textAlign: 'center', width: '80' },
          {
            title: '操作',
            field: '',
            textAlign: 'center',
            width: '70',
            type: "object",
            formatter: function(val, index, row, obj) {
              let template;
              let con = obj.$parent.controls;
              if(!con[3]) {
                return  {
                      template : '<div>--</div>'
                    }
              } else {
                template = '<button class="list-operate"  @click="openAccount">' + '  删除</button>';
                return {
                  template: template,
                  methods: {
                    openAccount: function() {
                      obj.$parent.delSolsClick(row);
                    }
                  }
                }
              }
            }
          },
        ],
        url: 'scheme/queryList',
        method: 'post',
       // url: 'dist/json/planList.json',
       // method: 'get',
        pageSize: 20,
        pageNumber: 1,
        pageList: [20, 40, 60]
      },
      nowRow: {},
      row: {},
    }
  },
  components: {
    edit
  },
  methods: {
    getFrom: function (val) {

       if (val.combine) {
            if (this.searchParam[val.key.split('.')[1]] !== val.val ) {
                 this.searchParam = this.cleanSearchData(this.searchParam,  ['schemeType']);
                 this.getOption(val, true);
                  this.getParams(1);
            }
       } else {
          this.getOption(val, true);
           this.getParams(1);
       }
      
    },
   
    closeDiv: function() {
      this.addOrInfo = false;
      this.getParams();
    },

    //刷新表格数据
    getParams: function(pageNumber) {
      this.deepSearchData = [];
      let arr = this.getSearchData(this.searchParam);
      this.getHeight();
      this.searchTable(arr, pageNumber);
    },

    //表格双击事件
    rowDbClick(items) {
      //双击方案详情界面
      this.row = items.val;
      this.addOrInfo = true;
    },
   
    //删除方案
    delSolsClick: function(row) {
      this.nowRow = row
      let confirm = this.$refs.conformDialog;
      let that = this;
      confirm.show({
        msg: `是否删除方案？`,
        action: {
          confirm:function(){
            that.delSolsFunc(row)
          }
        }
      });
    },
   

    //执行删除操作
    delSolsFunc: function(row) {
      let that = this;
      //请求详细信息
      this.getRequest({
        url: 'scheme/delete',
        method: 'post',
        param: {
          schemeFlag: row.schemeFlag
        },
        success: function(data) {
          that.alerts.show({ msg: data.message });
          that.closeDiv();
        },
        error: function(data) {
          that.alerts.show({ msg: data.message });
        }
      }, true);
    },

    windowChange: function() {
      let that = this;
      that.getHeight();
      /*window.onresize = function() {
        that.getHeight()
      }*/
    },
    init: function() {
      let that = this;
      /*window.onresize = function() {
        that.windowChange();
      }*/
    }
  },
  computed: {
    alerts: function() {
      return this.$refs.alerts
    }
  },
  mounted: function() {
    this.isIn();
    this.getAction();
  },
  watch: {
    'searchParam.isEnabled': {
      handler() {
        this.getParams(1);
      }
    }
  }
}

</script>
<style lang="scss">
@import "../../assets/main.scss";

</style>



