<template>
  <div class="info-div" style="height: 100%;">
    <div class="info-header">
      <span class="callback" @click="resetComponent">方案管理 </span>
      <span> > 方案详情</span>
    </div>
    <div class="info-container" v-show="show">
      <div class="list-content detail-content list-contentB" style="height:calc(100% - 1em)">
        <div class="list-header" style="border:0">
          <i class="hasIcon hasImg">
            <img src="dist/img/file/base-infor-icon.png" />
          </i> 方案信息
        </div>
        <div class="infoCons">
           <div class="infoL">
              <div class="infoLCon">
                <img src="dist/img/analyse/electric.png"/>
                <div>
                    <span class="hasColor">{{row.number}}</span>
                    <span >电表数量</span>
                </div>
              </div>
           </div>
           <ul class="form-list detail-list infoCen" >
             <li v-for="(item, $index) in list" v-if="!item.unShow">
               <span class=" label-span">{{item.name}} </span>
               <span class=" label-span"> &nbsp;:&nbsp;{{item.value}} </span>
             </li>
           </ul>
           <div class="infoR">
             <!--  <button class="list-operate" @click="deletePlan" v-if="controls[3]|| true">
                 <i class="hasIcon ">
                    <icon name="trash"></icon>
                 </i>
                 删除
                 v-if="(controls[2] || true) "
              </button> -->
              
              <button class="list-operate" @click="editPlan" v-if="controls[2]">
                <i class="hasIcon ">
                  <icon name="edit"></icon>
                </i>
                编辑
              </button>
              <button class="list-operate" @click="saveData" v-if="controls[2]">
                 <i class="hasIcon ">
                   <icon name="save"></icon>
                 </i>
                 提交
              </button>
           </div>
        </div>
        
      <div class="smallListB" >
        <div class="list-header" style="border-bottom:0;">
           <i class="hasIcon hasImg ">
              <img src="dist/img/analyse/electric_list.png" />
           </i> <span>方案电表列表</span>
           <!--v-if="formData.schemeType == 2" -->
           <button class="button-operate" @click="showSearchDevice" style="float:right" v-if="controls[2]">
              <i class="hasIcon hasImg ">
                 <img src="dist/img/analyse/add_madel.png" />
              </i>
              添加设备
            </button>
           <inputs ref="inputs"  :option="serachInput" @getInput="getSearch" style="float:right"></inputs>
           
        </div>
        <div ref="myOwnTable" class="table-container" style="height:calc(100% - 4em)">
            <tables ref="myTable" v-if="showTable"  :tableOpt="TableOpt" class="tablePadding" style="height: 100%;" @rowDbClick="getDetailInfo"></tables>
        </div>
      </div>
     </div>
    </div>
    <dialogs type="alert" ref="alerts" ></dialogs>
    <dialogs type="customer" ref="editPlan" width="38em" v-if="showEdit">
      <div slot="header">编辑方案基础信息</div>
      <div slot="content" >
        <ul class="dialog-ul">
          <li v-for="(item, $index) in dialogList" class="formList" :class="item.classes" v-if="!item.unShow">
            <label class="label-show" style="min-width:6.5em"><span v-if="!item.isEmpty" class="emptyTips"> * </span>{{item.name}}</label>
            <template v-if="item.style === 'input'">
              <inputs :ref="item.option.key" :option="item.option" @getInput="getFrom"></inputs>
            </template>
            <template v-if="item.style === 'select'">
              <selects :ref="item.option.key" :option="item.option" @getSelect="getFrom"></selects>
            </template>
             <template v-if="item.style === 'timerCombine'">
                <datePicker :ref="item.option[0].key" :dateOpt="item.option[0]" :showAfterDisabled="false" @getDatePicker="getFrom"></datePicker>
                <timers :ref="item.option[1].key" :timeOpt="item.option[1]" @getSelectTime="getFrom" style="margin-left:1em"></timers>
            </template>
          </li>
        </ul>
      </div>
      <div slot="footer">
        <button class="button-default" @click="saveEdit">确定</button>
        <button class="button-default cancel-button" @click="cancelEdit">取消</button>
      </div>
    </dialogs>
    <dialogs type="customer" ref="deviceInfo" width="36em" v-if="showDeviceInfo && formData.schemeType == 2">
       <div slot="header">设备通断详情</div>
       <div slot="content" >
           <table class="form-list table-list" style="margin:0.3em 0 0 0;width:100%">
              <tr class="table-header" >
                  <th style="border-bottom: 0;">操作</th>
                  <th style="border-bottom: 0;">操作日期</th>
                  <th style="border-bottom: 0;">操作时间</th>
             </tr>
          </table>
          <VuePerfectScrollbar class="tableBodyCon">
            <table class="form-list table-list" style="margin: 0 0 0.3em 0;width:100%">
             <tr v-for="(item, $index) in deviceOperateData" v-if="deviceOperateData.length > 0">
               <td >{{item.operateName}}</td>
               <td >{{item.startDate}}</td>
               <td >{{item.startTime}}</td>
            </tr>
           </table>
         </VuePerfectScrollbar>
       </div>
     </dialogs>
        <!--v-if="formData.schemeType == 2" -->
      <dialogs type="customer" ref="searchDevice" width="55em" maxHeight="600px">
       <div slot="header">添加设备</div>
       <div slot="content" >
          <inputs  :option="searchDeviceObj" @getInput="getFroms" style="float:right;margin-bottom:1em"></inputs>
          <tables ref="DiaTable" :tableOpt="dialogTableOpt" class="tablePadding" ></tables>
       </div>
     </dialogs>
  </div>
</template>
<script>
import timers from '../childComponent/DateTimer.vue'
import prefectScroll from 'perfect-scrollbar'
export default {
  data() {
    return {
      deviceOperateData:[],
      showDeviceInfo: false,
      show: true,
      title: '电价方案信息',
      formEditState: false,
      formModel: 0, //0添加 1详细
      formData: {
        schemeFlag: '',
        schemeName: '', //方案名称
        schemeType: '', //方案类别
        remark: '', //方案备注
        startDate: '',
        startTime: '',
        endDate: '',
        endTime: ''
      },
      showTable: false,
      showEdit: false,
      showSave: false,
      solsItem: [
      ],
      width: '',
      TableOpt: {
          column: [],
          data: [],
          pageSize: 20,
          pageNumber: 1,
          pageList: [20, 40, 60],
          isExport: true,
          exportURL: 'scheme/downloadMeterList',
          noExportCurrent: true
      },
      dialogTableOpt: {
          column: [
              {title: '用户户名', field: 'customerName', textAlign: 'center', width: '80'},
              {title: '用户户号', field: 'meterUserNo', textAlign: 'center', width: '70'},
              {title: '用户表号', field: 'deviceNo', textAlign: 'center', width: '80'},
              {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '80'},
              {title: '楼栋', field: 'buildNo', textAlign: 'center', width: '80'},
              {title: '变压器', field: 'transformerNo', textAlign: 'center', width: '80'},
              {title: '操作', field: '', textAlign: 'center', width: '70', 
                type:"object",
                formatter: function (val, index, row, obj) {
                  let template;
                  let text = '添加';
                  let color = '#69A4E7!important';
                  if(row.disabled) {
                    text = "已添加";
                    color = "#A26744!important"
                  }
                  template = '<button class="list-operate" style="color:'+color+'" @click="open">' + '  ' + text +'</button>';
                  return {
                    template: template,
                    methods: {
                      open: function () {
                        if(!row.disabled) {
                            obj.$parent.$parent.addDevice(row);
                        }
                        obj.$set(row, 'disabled', true);
                      }
                    }
                  }
                 }
                }
          ],
          hasLoad: true,
          data: [],
          pageSize: 20,
          height: 300,
          pageNumber: 1,
          pageList: [20, 40, 60]
      },
      dialogSearchParam: {
         deviceNo: ''
      },
      searchParam: {
         deviceNo: ''
      },
      serachInput: {
              type: 'typeSearch',
              key: 'searchParam',
              selectOpt: {
                width: '8em',
                cs: 'client',
                data: [
                        {text: '用户表号', value: 'deviceNo'}
                      ],
                key: 'searchType'
              }
      },
     
    }
  },
  computed: {
     //弹框中的device搜索框
      searchDeviceObj: function () {
        return {
          type: 'typeSearch',
          key: 'dialogSearchParam',
          placeholder: '至少四位数字',
          length: 16,
          limit: 'number',
          value: this.dialogSearchParam.deviceNo,
          selectOpt: {
            width: '8em',
            cs: 'client',
            data: [
                {text: '用户表号', value: 'deviceNo'}
            ],
            key: 'searchType'
          }
        }
    },
    column: function () {
       return [
              {title: '用户户名', field: 'customerName', textAlign: 'center', width: '80'},
              {title: '用户户号', field: 'meterUserNo', textAlign: 'center', width: '70'},
              {title: '用户表号', field: 'deviceNo', textAlign: 'center', width: '80'},
              {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '80'},
              {title: '楼栋', field: 'buildNo', textAlign: 'center', width: '80'},
              {title: '变压器', field: 'transformerNo', textAlign: 'center', width: '80'},
              {title: '操作', field: '', textAlign: 'center', width: '70', 
              type:"object",
              formatter: function (val, index, row, obj) {
                  let template;
                  let con = obj.$parent.$parent.controls;
                  if(!con[2]) {
                    return {
                      template : '<div>--</div>'
                    }
                  } else {
                    template = '<button class="list-operate"  @click="openAccount">' + '  移除</button>';
                    return {
                      template: template,
                      methods: {
                        openAccount: function () {
                          obj.$parent.removeDevice(row);
                        }
                      }
                    }
                  }
                }
              }
          ]
    },
    alerts: function() {
      return this.$refs.alerts
    },
    dialogList: function () {
      let width = 'calc(100% - 8em)';
      let widths = 'calc((100% - 9em) / 2)';
      return [
                   {
                      name: '方案名称',
                      style: 'input',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: {
                        key: 'formData.schemeName',
                        width: width,
                        value: this.formData.schemeName,
                        type: 'normalInput',
                        length: 100
                      }
                   },
                   {
                      name: '方案类型',
                      style: 'select',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: {
                        key: 'formData.schemeType',
                        cs: 'server',
                        url: 'dictItem/queryByDictCode',
                        param: {dictCode: '16'},
                        text: 'dictItemName',
                        value: 'dictItemValue',
                        method: 'post',
                        width: width,
                        constant: {dictItemValue: this.formData.schemeType},
                      }
                   },
                  {
                      name: '通电时间',
                      style: 'timerCombine',
                      unShow: this.formData.schemeType != '2',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: [{
                            key: 'formData.startDate',
                            width: widths,
                            value: this.formData.startDate || ''
                         },
                         {
                            key: 'formData.startTime',
                            width: widths,
                            value: this.formData.startTime
                         }
                      ]
                   },
                   {
                      name: '断电时间',
                      style: 'timerCombine',
                      unShow: this.formData.schemeType != '2',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: [{
                            key: 'formData.endDate',
                            width: widths,
                            value: this.formData.endDate || ''
                         },
                         {
                            key: 'formData.endTime',
                            width: widths,
                            value: this.formData.endTime
                         }
                      ]
                   },
                   {
                      name: '方案备注',
                      style: 'input',
                      classes: 'allWidth',
                      isEmpty: true,
                      option: {
                        key: 'formData.remark',
                        width: width,
                        value: this.formData.remark,
                        type: 'normalInput',
                        length: 200
                      }
                    } 
                ]
      },
    list: function() {
      return [{
          name: '创建人员',
          value: this.formData.creator,
        },
        {
          name: '方案名称',
          value: this.formData.schemeName,
        },
        {
          name: '创建时间',
          value: this.formData.createTime,
        },
        {
          name: '通电时间',
          unShow: this.formData.schemeType != 2,
          value: (this.formData.startDate || '')  + ' '+( this.formData.startTime || ''),
        },
        {
          name: '断电时间',
          unShow: this.formData.schemeType != 2,
          value: (this.formData.endDate || '' ) + ' '+ (this.formData.endTime || ''),
        },
        {
          name: '方案类型',
          value:  this.formData.schemeTypeName,
        },
        {
          name: '方案备注',
          value: this.formData.remark
        }
      ]
    }
  },
  components: {
     timers
  },
  props: ['controls', 'row'],
  methods: {
    getFroms: function (val) {
      if(val.val.toString().length > 3) {
         if(this.dialogSearchParam.deviceNo != val.val) {
          this.getOption(val, this);
          this.$refs.DiaTable.resetTableOpt({queryParam: this.dialogSearchParam, url: 'scheme/deviceUserList', method: 'post'});
        }
      } else if(val.val.toString() > 0){
        this.$refs.alerts.show({msg: '至少输入4位电表表号'});
      }
    },
    addDevice: function (row) {
      let arr = [];
      arr.push(row);
      this.$refs.myTable.operateData(arr, 'cno', 0, false, true);
    },

    showSearchDevice: function () {
       let that = this;
       that.dialogSearchParam.deviceNo = '';
       that.$refs.DiaTable.resetTableOpt({data: [], url: ''});
       that.$nextTick(function () {
          that.$refs.searchDevice.show({
             hasClose: true
          });
        });
    },
    getDetailInfo: function (val) {
       let row = val.val;
       let that = this;
     //  if(row.id) {
         that.getRequest({
           url: 'scheme/deviceCount',
           method: 'post',
           param: {cno: row.cno},
           success: function (data) {
            that.deviceOperateData = data.data;
           }
         }, true);
         that.showDeviceInfo = true;
         that.$nextTick(function () {
           that.$refs.deviceInfo.show({
            hasClose: true,
            action: {
             confirm: function () {
               that.showDeviceInfo = false;
             }
           }
         });
       }, true);
     //}
    },
    removeDevice: function (row) {
      let arr = [];
      arr.push(row);
      let flag = false;
      if(this.searchParam.deviceNo) {
        flag = true;
      }
      this.$refs.myTable.operateData(arr, 'cno', 1, flag, true);
    },
    saveData: function () {
       let that = this;
       if((that.formData.startDate != '' && that.formData.startTime !== '') || (that.formData.endDate != '' && that.formData.endTime !== '') || this.formData.schemeType != '2') {
         let tableData = this.$refs.myTable.getOperateData();
         if(this.$refs.myTable.getDealData().length == 0) {
              that.$refs.alerts.show({msg: '设备列表不能为空！'});
              return 
         }
         let param = this.getSearchData(this.formData);
         let arr = [];
         for(let i =0; i<tableData.length; i++) {
            let item = tableData[i];
            arr.push({customerNo: item.customerNo, deviceNo: item.deviceNo, cno: item.cno, meterUserNo: item.meterUserNo, id: item.id || '', flag: item.flag})
          }
           param['meterList'] = arr;
           that.getRequest({
            url: 'scheme/edit',
            method: 'post',
            param: param,
            success: function (data) {
              that.alerts.show({msg: data.message});
              setTimeout(function () {
                that.resetComponent();
              }, 1000)
            },
            error: function (data) {
              that.alerts.show({msg: data.message})
            }
          })
       } else {
         that.$refs.alerts.show({msg: '通电时间或者断电时间至少填写一项'});
       }
      
    },
    getDeviceInfo: function () {
      let that = this;
      let param = {schemeFlag: that.row.schemeFlag, pageNumber:1, pageSize: 99999999};
      that.getRequest({
        url: 'scheme/queryMeterList',
        method: 'post',
        // url: 'dist/json/planList2.json',
        // method: 'get',
        param: param,
        success: function (data) {
           that.TableOpt.data = data.data;
           that.TableOpt['queryParam'] = param;
           that.showTable = true
        },
        error: function (data) {
          that.TableOpt.data = [];
          that.showTable = true
        }
      })
    },
    getSearch: function (val) {
      if(this.searchParam[val.key.split('.')[1]] !== val.val){
         this.searchParam = this.cleanSearchData(this.searchParam);
         this.getOption(val, this);
         this.$refs.myTable.searchTable(this.searchParam);
      }
    },
    saveEdit: function () {
      let that = this;
 
         
      if(!((that.formData.startDate != '' && that.formData.startTime !== '') || (that.formData.endDate != '' && that.formData.endTime !== '') || that.formData.schemeType != '2')) {
         that.$refs.alerts.show({msg: '通电时间或者断电时间至少填写一项'});
         return;
      }
      if(that.formData.schemeName === ''){
         that.$refs.alerts.show({msg: '方案名称不能为空！'});
         return;
      }
      this.showEdit = false;
      
    },
    cancelEdit: function () {
      this.showEdit = false;
      this.copyObj();
    },
    getFrom: function(val) {
      this.getOption(val, this);
      if(val.key == 'formData.schemeType') {
         this.formData.schemeTypeName = val.text
      }
    },
    resetComponent: function() {
      let that = this;
      that.show = false;
      that.$emit('closeDiv');
      setTimeout(function() {
        that.show = false;
      }, 0)
    },
    deletePlan: function () {
      this.$parent.delSolsClick(this.row);
    },
    editPlan: function () {
      this.showEdit = true;
      this.showSave  = true;
      this.$refs.myTable.resetTableOpt({column: this.column, isLoadData: false});
      let that = this;
      that.$nextTick(function () {
         that.$refs.editPlan.show();
      })
    },
   
  
    init: function() {
    
    },
    copyObj: function () {
     for(let key in this.row) {
      if(key == 'desc') {
         this.formData['remark'] =  this.row[key];
      } else {
         this.formData[key] =  this.row[key];
      }
     }
   }
  },
  mounted: function() {
    this.isIn();
    this.init();
  },
  created: function () {
    this.copyObj();
    this.getDeviceInfo();
    this.TableOpt.column = this.column;
  }
}

</script>
<style lang="scss" scoped>
.EditorPrice {
  width: 100%;
  position: relative;
}

header {
  position: relative;
}
.smallListB{
  height: calc(100% - 8em);
  float:left;
  .list-header{
    margin: 0;
    .hasIcon{
      margin-top: 0.5em
    }
    span{
      line-height:2.6;
      margin-left:0.4em
    }
    .button-operate{
        float: right;
        line-height: 2;
        margin:0 0 0 0.5em;
        .hasIcon{
          margin-top:0.2em;
          width:1.7em;
          height:1.7em!important
        }
    }
  }
}
.smallListB:after{
  content:'';
  width:0.1px;
  height:0;
  clear:both;
  display:block
}
.infoCons{
  display: flex;
  padding-bottom:0.5em;
  border-bottom: 1px solid rgba(255,255,255,0.1);
  .infoL {
     width: 8em;
     display:table;
     padding:0.5em 2em 0.5em 3em;
     border-right: 1px solid rgba(255,255,255,0.1);
     flex: 1;
     margin-top:0.3em;
    .infoLCon{
      display:table-cell;
      margin: auto;
      vertical-align: middle;
      img{
         width: 2.5em;
         height: 2.8em;
         float:left;
         margin-top:0.2em;
         margin-right:0.5em;
      }
      div{
        float:left;
        width: calc(100% - 3em);
        height:100%;
      }
      span{
        display: block;
        float:left;
        width: 100%;
        font-size: 0.83em;
        text-align:center;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
      span.hasColor{
        color: #3E8EDE;
        font-size: 1.8em;
        font-weight: 600
      }
    }
  }
  .infoCen {
    width: calc((100% - 25em));
    display: inline-block;
    margin-left:1em;
  }
  .infoR {
    display: inline-block;
    width:10em;
    button{
      display: flex;
      font-size: 0.83em;
      margin:auto
    }
  }
}
.tableBodyCon{
  max-height:200px;
  width:100%;
  position:relative;
  float:left;
  box-shadow: 0 1px 3px rgba(255,255,255,0.1)
}


</style>



